import React, { useEffect, useState } from 'react';
import defaultData from '../defaultData';

interface ContactProps extends React.HTMLProps<HTMLDivElement> {}

const Contact: React.FC = props => {
  // 動的にリサイズする可能性があるため state で値を管理しておく
  const [iframeWidth, setIframeWidth] = useState(640);
  const [iframeHeight, setIframeHeight] = useState(1152);
  const [isDisplayForm, setIsDisplayForm] = useState(false);

  useEffect(() => {
    setIsDisplayForm(/displayForm/.test(window.location.search));
  }, []);

  return (
    <section id='contact'>
      <div className='container'>
        <h1 className={isDisplayForm ? 'd-none' : ''}>
          {defaultData.contact.title}
        </h1>
        <div className='contact-background'>
          <div className='row'>
            <div className='col-10 offset-1 col-lg-8 offset-lg-2'>
              <p className={isDisplayForm ? 'd-none' : 'text-center'}>
                購入、売却、その他なんでもお気軽にお問い合わせください。
              </p>
              <div className='text-center'>
                <a
                  href='https://forms.gle/hh4mNqLmxFar7krb6'
                  target='_blank'
                  role='button'
                  className={`btn btn-primary rounded mt-5 px-5 py-3 h6${
                    isDisplayForm ? ' d-none' : ''
                  }`}
                >
                  お問い合わせ
                </a>
              </div>
              <iframe
                className={`inquiry-form${isDisplayForm ? '' : ' d-none'}`}
                style={{ width: '100%' }}
                src='https://docs.google.com/forms/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/viewform?embedded=true'
                width={iframeWidth}
                height={iframeHeight}
                frameBorder={0}
                marginWidth={0}
                marginHeight={0}
              >
                読み込んでいます…
              </iframe>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
